(function () {

///////////////////////////////////////////////////////////////////////
//                                                                   //
// packages/kadira:dochead/lib/both.jsx.js                           //
//                                                                   //
///////////////////////////////////////////////////////////////////////
                                                                     //
var FlowRouter = null;
if (Package['kadira:flow-router-ssr']) {
  FlowRouter = Package['kadira:flow-router-ssr'].FlowRouter;
}

DocHead = {
  currentTitle: null,
  setTitle: function (title) {
    if (Meteor.isClient) {
      document.title = title;
    } else {
      this.currentTitle = title;
      var titleHtml = '<title>' + title + '</title>';
      this._addToHead(titleHtml);
    }
  },
  addMeta: function (info) {
    this._addTag(info, 'meta');
  },
  addLink: function (info) {
    this._addTag(info, 'link');
  },
  getTitle: function () {
    if (Meteor.isClient) {
      return document.title;
    } else {
      return this.currentTitle;
    }
  },
  loadScript: function (url, options, callback) {
    if (Meteor.isClient) {
      LoadScript(url, options, callback);
    }
  },
  _addTag: function (info, tag) {
    if (Meteor.isClient) {
      var meta = this._buildTag(info, tag);
      $('head').append(meta);
    } else {
      var meta = this._buildTag(info, tag);
      this._addToHead(meta);
    }
  },
  _addToHead: function (html) {
    // only work there is kadira:flow-router-ssr
    if (!FlowRouter) {
      return;
    }

    var ssrContext = FlowRouter.ssrContext.get();
    if (ssrContext) {
      ssrContext.addToHead(html);
    }
  },
  _buildTag: function (metaInfo, type) {
    var props = "";
    for (var key in metaInfo) {
      props += key + '="' + metaInfo[key] + '" ';
    }
    props += 'dochead="1"';
    var meta = '<' + type + ' ' + props + '/>';
    return meta;
  },
  removeDocHeadAddedTags: function () {
    if (Meteor.isClient) {
      $('[dochead="1"]', document.head).remove();
    }
  }
};///////////////////////////////////////////////////////////////////////

}).call(this);
